import Vue from 'vue'; // Vue
import CoMessage from './src'; // message

const MessageBox = Vue.extend(CoMessage); // Vue.extend
// let instance; // instance 

// functionoptions
const Message = function (options = {}) {
    // 
    if (Vue.prototype.$isServer) return;
    // message-boxmessage-box
    // if (timer) return;
    // optionsstringoptionsmessage
    if (typeof options === 'string') {
        options = {
            message: options
        }
    }
    // optionsdataVueoptionsdataoptions
    const instance = new MessageBox({
        data: options
    });
    // $mountDOM$el $el DOM
    instance.vm = instance.$mount();
    // jsAPIDOMbody
    document.body.appendChild(instance.vm.$el);
    // vmVuevm
    // visibletruemessage-box
    instance.vm.visible = true;
    // 
    instance.vm.timer = setTimeout(() => {
        instance.vm.top = -56
        setTimeout(() => {
            instance.vm.visible = false;
        }, 600)
        // instance.vm.visible = false;

        // setTimeout(() => {      //      bug
        //     instance.vm.$destroy();      // 
        //     instance.vm.$el.parentNode.removeChild(instance.vm.$el);     // APIDOMDOM
        // }, 1000)


        // 
        instance.vm.timer = null;
    }, instance.vm.duration);
    // vm
    return instance.vm;
};
//  install Vue
// installVue
// messageaxios
export default {
    message: Message,
    install(Vue) {
        Vue.prototype.$message = Message;
        Vue.message = Message;
    }
};
