export default {
    methods: {
        mixin_isGroup(componentTag) {
            // parent
            let parent = this.$parent;
            // group
            while (parent) {
                if (parent.$options._componentTag !== componentTag) {
                    parent = parent.$parent;
                } else {
                    this['_Group'] = parent;
                    return true;
                }
            }
            return false;
        },
    },
}