const CheckboxGroupStore = function (form, initialState = {}) {
    // Store
    if (!form) {
        throw new Error('Form is required.')
    }
    this.form = form

    this.states = {
        checkboxList: [],
    }
    // initialState  this.statesstorestore
    for (let prop in initialState) {
        if (Object.prototype.hasOwnProperty.call(initialState, prop) && Object.prototype.hasOwnProperty.call(this.states, prop)) {
            this.states[prop] = initialState[prop]
        }
    }
}

CheckboxGroupStore.prototype.mutations = {
    addCheckbox(states, checkbox) {
        states.checkboxList.push(checkbox)
    },
    removeCheckbox(states, checkbox) {
        const index = states.checkboxList.indexOf(checkbox)
        states.checkboxList.splice(index, 1)
    },
    clear(states) {
        states.checkboxList = []
    }
}


// 
CheckboxGroupStore.prototype.commit = function (name, ...args) {
    const mutations = this.mutations
    console.log('emit', name)
    if (mutations[name]) {
        // states 
        mutations[name].apply(this, [this.states].concat(args))
    } else {
        throw new Error(`Action not found: ${name}`)
    }
}
// 
// this.store.commit('setFormItemToHandle', val)
export default CheckboxGroupStore